@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto:wght@500;700&display=swap";
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(100px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.special-offers_section {
  background-color: #fff;
}

@media (width <= 768px) {
  .special-offers_section {
    padding: 22px 16px;
  }
}

@media (width >= 768px) {
  .special-offers_section {
    padding: 45px 80px;
  }
}

.special-offers_section .special-offers_title {
  color: #3fa6bc;
  text-align: center;
  margin-bottom: 45px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

@media (width <= 768px) {
  .special-offers_section .special-offers_title {
    font-size: 32px;
  }
}

@media (width >= 768px) {
  .special-offers_section .special-offers_title {
    font-size: 64px;
  }
}

@media (width <= 768px) {
