    gap: 15px;
  }
}

.special-offers_section .special-offer_item {
  z-index: 1;
}

.special-offers_section .special-offer_item .special-offer_title {
  color: #fff;
  background-color: #8cc5d2;
  margin-bottom: 17px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  display: inline-block;
}

@media (width <= 768px) {
