  }
}

@media (width <= 768px) {
  .special-offers_section .special-offer_item .special-offer_title {
    font-size: 17px;
  }
}

@media (width >= 768px) {
  .special-offers_section .special-offer_item .special-offer_title {
    font-size: 23px;
  }
}

@media (width <= 768px) {
  .special-offers_section .special-offer_item .special-offer_title {
    margin-bottom: 10px;
  }
}

.special-offers_section .special-offer_item .special-offer_description {
  color: #000;
  text-align: left;
  font-family: Montserrat, sans-serif;
