  }
}

@media (width >= 768px) {
  .special-offers_section .special-offer_item .special-offer_description {
    font-size: 23px;
  }
}

body:after {
  content: "";
  filter: blur(10px);
  pointer-events: none;
  background: linear-gradient(#e0edef00, #e0edef);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay {
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  background-color: #00000080;
  width: 100vw;
  height: 100vh;
  margin-top: 100px;
  transition: opacity .6s, visibility .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  opacity: 1;
  visibility: visible;
}
